import Vue from 'vue';
import Vuex from 'vuex';
Vue.use(Vuex);
var code = localStorage.getItem("code")
const state = {
    count:1
}
// const getters = {
//     count:function(state){
//         return state.count +=100;
//     }
// }
export default new Vuex.Store({
    state, // data
    // getters,// mounted
    mutations:{ // methods 
        add(state) {
            state.count+=1;
        },
        dele(state) {
            state.count-=1;
        }
    }
});


