import Vue from 'vue'
import Router from 'vue-router'
import weather from '../project/weather.vue'
import administration from '../project/administration.vue'
import searchcity from '../project/searchcity.vue'

Vue.use(Router)

export default new Router({
  model:history,
  routes: [
    {
      path: '/',
      name: 'administration',
      component: administration
    },
    {
      path: '/weather',
      name: 'weather',
      component: weather
    },
    {
      path: '/administration',
      name: 'administration',
      component: administration
    },
    {
      path: '/searchcity',
      name: 'searchcity',
      component: searchcity
    },
  ]
})



