<template>
  <div class="administration">
      <!--  -->
  <mt-header fixed title="">
        <router-link to="/weather" slot="left">
            <mt-button icon="back"></mt-button>
        </router-link>
    </mt-header>

    <div class="info" v-for="(item,index) in weather" :key="item.index" @click="keydown(index)">
        <div class="dele" @click.stop="delecity(index)">
            <img src="../assets/weather/delete.png" alt="">
        </div>
        <div style="height:40px;">
            {{item.city}}
        </div>
        <span>{{item.weather}}</span>
    </div>
     <!--  -->
    <div class="addcity">
        <router-link to='/searchcity' href="#">
            <img src="../assets/weather/iconfont-line3.png" alt="">
        </router-link>
    </div>
  </div>
</template>
<script>

export default {
    inject:['reload'],
  name: 'administration',
  data () {
    return {
      msg: '',
      weather:[],
      cityname:[]
    }
  },
  created(){
    //  this.getcitycode()
     this.ajax()
    },
  methods:{
      delecity(index){
        var cityindex = localStorage.getItem("code").split(",")
        cityindex.splice(index,1)
        // console.log(cityindex[index]) // 101250509,101070206,101190703
        localStorage.setItem("code",cityindex)
        // location.reload()
        this.reload()
      },
    keydown(index) {
        // alert(index)
        // url
        this.$router.push({
          path: `/weather/?index=${index}`,
        })
        // window.location = "http://192.168.88.69:8080/#/weather?index=" + index 
    },
     back(){
         this.$router.push({
      path: '/weather'
    })
        //  window.location = "http://192.168.88.69:8080/#/weather"
     },
    //  getcitycode() {
    //     // var citycode = localStorage.getItem('code')
    //         var citycode = localStorage.getItem('code')
    //         var code = citycode.split(',')
    //     for(var i = 0;i<code.length;i++) {
    //         var codeid = code[i]
    //         console.log(codeid)
    //     this.axios.get('/apis/jmportalwb/interfaces/weather_s.do', {
    //         params: {
    //             citycode: codeid,
    //         }
    //     })
    //       .then( res => {
    //         // console.log(this.weather)
    //         var data = res.data.result.sk // 
    //         // 
    //         var city = data.city
    //         var weather = data.weather
    //         console.log("city>>>>>"+city)
    //         this.weather.push({
    //           "city":city,
    //           "weather":weather,
    //         })
    //         console.log(this.weather)
    //       })
    //       .catch(function (error) {
    //       console.log(error);
    //       });
    //     }
    //  }
    ajax() {
        var citycode = localStorage.getItem('code')
            var code = citycode.split(',')
            for(var i = 0;i<code.length;i++) {
            var codeid = code[i]
        $.ajax({
			type : "get",
            url : 'http://jmpotal.hanweb.com/jmp/interfaces/weather_new.do',
            async : false,
			data : {
				citycode:codeid
			},
			dataType : 'json',
			success : function(data) {
               var data = data.result.sk
            //    console.log(data)
               var obj = {}
               obj.city = data.city
               obj.weather = data.weather
               this.weather.push(obj)
            //    console.log(this.weather)
			}.bind(this),
			error : function(data) {
				alert("");
			}
        });
    }
    }
  }
}

