</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
#btn{
    margin-top: 80px;
    height: 50px;
    width: 100px;
}
    .administration {
        background: url(../assets/weather/leizhenyu.jpg);
        background-size: cover;
        margin-top: -60px;
        height: 700px;
        padding: 8px;
        overflow: hidden;
        overflow-y:scroll;
        
    }
/*   */
    .mint-header.is-fixed {
        font-size: 18px;
        height: 44px;
        background-color: rgb(35, 39, 54);
    }
    /*  */
    .addcity {
        width: 45%;
        height: 90px;
        background-color: rgba(0, 0, 0, 0.5);
        float: left;
        margin-top: 46px;
        margin-left: 15px;
        overflow: hidden;
        line-height: 110px;
    }
    .addcity a{
        display: inline-block;
        width: 100%;
        height: 100%;
    }
    .addcity img{
        width: 40px;
        height: 40px;
    }
    .className {
        background-color: red;
    }
    .info{
        width: 45%;
        height: 90px;
        background-color: rgba(0, 0, 0, 0.5);
        float: left;
        margin-top: 46px;
        margin-left: 15px;
        overflow: hidden;
        line-height: 60px;
        color: #fff;
        position: relative;
    }
    .info .dele {
        width: 20px;
        height: 20px;
        position: absolute;
        top: -10px;
        right: 5px;
    }
     .info .dele img {
          width: 20px;
        height: 20px;
        z-index: 1000;
     }
</style>



