<script>

export default {
  name: 'weather',
  data () {
    return {
      msg: '',
      weather:[],
      weekone:[],
      week2:[],
      week3:[],
      todayData:[],
      Clothingindex:[],
      car:[],
      ganmao:[],
      spaort:[],
      ziwaixian:[],
    }
  },
  methods:{
  
  },
  created(cityname) {
    
      // localStorage.setItem("code",101190101)
      var citycode = localStorage.getItem("code")
      //  this.$route.query.index localStoragecodezhi
      var index = this.$route.query.index
        var code = citycode.split(',')
      // console.log("code>>>>>>>>"+code[index])
      var codeid = code[index]

      if(localStorage.getItem("code") && !index) {
        var lastcode = localStorage.getItem("code").split(",")
        for(var i=0;i<lastcode.length;i++) {
          console.log(lastcode[lastcode.length-1])
        }
        var lastid = lastcode[lastcode.length-1]
      }
      
      // alert("ll>>>>>>>>"+this.$route.query.index)
      if(localStorage.getItem("code")) { 
        this.axios.get('http://jmpotal.hanweb.com/jmp/interfaces/weather_new.do', {
          params: {
            citycode: lastid,
          }
        })
          .then( res => {
            // localStorage.removeItem("code")
            // console.log(this.weather)
            var data = res.data.result.sk // 
            var afterTherrDays = res.data.result.future //
            var data2 = res.data.result
            var today = res.data.result.index // 
            // 
            // console.log(today)
            for(var i = 0;i<today.length; i++) {
              var Clothingindex = today[0]
              var car = today[1]
              var ganmao = today[2]
              var spaort = today[3]
              var ziwaixian = today[4]
            }
              this.Clothingindex.push(Clothingindex)
              this.car.push(car)
              this.ganmao.push(ganmao)
              this.spaort.push(spaort)
              this.ziwaixian.push(ziwaixian)
            // 
            this.afterTherrDays = afterTherrDays
            // console.log(this.afterTherrDays)
            for (var i =0;i < this.afterTherrDays.length; i++) {
              var weekone = (this.afterTherrDays[0])
              var week2 = (this.afterTherrDays[1])
              var week3 = (this.afterTherrDays[2])
            }
            // 
            var week = weekone.week
            var weather = weekone.weather
            var daypicurl = weekone.daypicurl

            this.weekone.push({
              week:week,
              weather:weather,
              daypicurl:require("../assets/weather/"+daypicurl),
            })
            // 
            var week = week2.week
            var weather = week2.weather
            var daypicurl = week2.daypicurl

            this.weekone.push({
              week:week,
              weather:weather,
              daypicurl:require("../assets/weather/"+daypicurl),
            })
            // 
            var week = week3.week
            var weather = week3.weather
            var daypicurl = week3.daypicurl

            this.weekone.push({
              week:week,
              weather:weather,
              daypicurl:require("../assets/weather/"+daypicurl),
            })
            this.week3.push(week3)
            // 
            var city = data.city
            var daypicurl = data.daypicurl
            var nightpicurl = data.nightpicurl
            var pm25 = data.pm25
            var temp = data.temp
            var time = data.time
            var weather = data.weather
            var wind = data.wind
            var f_time = data.date

            this.weather.push({
              city:city,
              daypicurl:require("../assets/weather/"+daypicurl),
              nightpicurl:require("../assets/weather/"+nightpicurl),
              pm25:pm25,
              temp:temp,
              time:time,
              weather:weather,
              wind:wind,
              f_time:f_time
            })
            // console.log(this.weather)
          })
          .catch(function (error) {
          console.log(error);
          });
      } // citycode localstaorecode

        
        this.axios.get('http://jmpotal.hanweb.com/jmp/interfaces/weather_new.do', {
          params: {
            citycode: codeid,
          }
        })
          .then( res => {
            // localStorage.removeItem("code")
            // console.log(this.weather)
            var data = res.data.result.sk // 
            var afterTherrDays = res.data.result.future //
            var data2 = res.data.result
            var today = res.data.result.index // 
            // 
            // console.log(today)
            for(var i = 0;i<today.length; i++) {
              var Clothingindex = today[0]
              var car = today[1]
              var ganmao = today[2]
              var spaort = today[3]
              var ziwaixian = today[4]
            }
              this.Clothingindex.push(Clothingindex)
              this.car.push(car)
              this.ganmao.push(ganmao)
              this.spaort.push(spaort)
              this.ziwaixian.push(ziwaixian)
            // 
            this.afterTherrDays = afterTherrDays
            // console.log(this.afterTherrDays)
            for (var i =0;i < this.afterTherrDays.length; i++) {
              var weekone = (this.afterTherrDays[0])
              var week2 = (this.afterTherrDays[1])
              var week3 = (this.afterTherrDays[2])
            }
            // 
            var week = weekone.week
            var weather = weekone.weather
            var daypicurl = weekone.daypicurl

            this.weekone.push({
              week:week,
              weather:weather,
              daypicurl:require("../assets/weather/"+daypicurl),
            })
            // 
            var week = week2.week
            var weather = week2.weather
            var daypicurl = week2.daypicurl

            this.weekone.push({
              week:week,
              weather:weather,
              daypicurl:require("../assets/weather/"+daypicurl),
            })
            // 
            var week = week3.week
            var weather = week3.weather
            var daypicurl = week3.daypicurl

            this.weekone.push({
              week:week,
              weather:weather,
              daypicurl:require("../assets/weather/"+daypicurl),
            })
            this.week3.push(week3)
            // 
            var city = data.city
            var daypicurl = data.daypicurl
            var nightpicurl = data.nightpicurl
            var pm25 = data.pm25
            var temp = data.temp
            var time = data.time
            var weather = data.weather
            var wind = data.wind
            var f_time = data.date
            this.weather.push({
              city:city,
              // daypicurl:'../assets/weather/'+daypicurl,
              daypicurl:require("../assets/weather/"+nightpicurl),
              nightpicurl:require("../assets/weather/"+nightpicurl),
              pm25:pm25,
              temp:temp,
              time:time,
              weather:weather,
              wind:wind,
              f_time:f_time
            })
          })
          .catch(function (error) {
          console.log(error);
          });
      
      
    }
  }
</script>

