<script>
export default {
  name: 'searchcity',
  data () {
    return {
      msg: '',
      citys:[],
    }
  },
  methods:{
    selectedcity(cityid,cityname){
        console.log(cityid)
        console.log(cityname)
        // localStorage 
        if(localStorage.getItem("code")) {
            var citycode = localStorage.getItem("code") 
            citycode += "," + cityid;
            localStorage.setItem("code",citycode)
        }else{
            localStorage.setItem("code",cityid)
        }
        this.$router.push({path: '/administration'})
    }
  },
  created() {
        //   http://192.168.88.21:8080/jmportal4.0.0/interfacesarea_new.do
      this.axios.get('http://jmpotal.hanweb.com/jmp/interfaces/area_new.do', {
          params: {
          }
        })
          .then( res => {
            console.log(res)
            var city = res.data.citys
            for(var i = 0;i < city.length; i++) {
            // console.log(city[i])
                var acronym = city[i].acronym;
				var cityid = city[i].cityid;
				var cityname = city[i].cityname;
                var firstletter = city[i].firstletter;
                this.citys.push({
                    acronym:acronym,
                    cityid:cityid,
                    cityname:cityname,
                    firstletter:firstletter,
                })
            }
            //   console.log(this.citys)
        })
          .catch(function (error) {
          console.log(error);
        });
    }
}
</script>

